  .lgf-progress  {
       width: 150px;
       height: 150px;
       line-height: 150px;
       background: none;
       margin: 0 auto;
       box-shadow: none;
       position: relative;
  }
  .lgf-progress-small  {
       width: 70px !important;
       height: 70px !important;
   }
  
   .lgf-progress:after {
       content: "";
       width: 100%;
       height: 100%;
       border-radius: 50%;
       border: 15px solid #eee;
       position: absolute;
       top: 0;
       left: 0;
  }
  .lgf-progress-small:after {
    border: 7px solid #eee;
  }
  .lgf-progress > span {
       width: 50%;
       height: 100%;
       overflow: hidden;
       position: absolute;
       top: 0;
       z-index: 1;
  }
  .lgf-progress-text{
    margin-top: -10% !important;
    line-height: 0;
    font-weight: 700;
  }
  .lgf-progress-text h6 {
    padding: 0 !important;
    line-height: 1.5;
    font-size: 1.5rem;
    font-weight: 700 !important;
    margin: 0 !important;
  }
  
  .lgf-progress-small .lgf-progress-text h6 {
    font-size: 0.9rem;
    font-weight: 600 !important;
  }
  
  .lgf-progress-text span {
      font-size: 0.6rem !important;
      width: 100%;
  }
  
   .lgf-progress .lgf-progress-left {
       left: 0;
  }
   .lgf-progress .lgf-progress-bar {
       width: 100%;
       height: 100%;
       background: none;
       border-width: 15px;
       border-style: solid;
       position: absolute;
       top: 0;
  }
  .lgf-progress-small .lgf-progress-bar {
    border-width: 7px;
  }
  
  .lgf-progress .lgf-progress-bar.lgf-progress--success {border-color: #92fb9f;}
  .lgf-progress .lgf-progress-bar.lgf-progress--orange {border-color: #fe721c;}
  .lgf-progress .lgf-progress-bar.lgf-progress--warning {border-color: #ffca44;}
  .lgf-progress .lgf-progress-bar.lgf-progress--blue {border-color: #003696;}
   .lgf-progress .lgf-progress-left .lgf-progress-bar {
       left: 100%;
       border-top-right-radius: 75px;
       border-bottom-right-radius: 75px;
       border-left: 0;
       -webkit-transform-origin: center left;
       transform-origin: center left;
  }
   .lgf-progress .lgf-progress-right {
       right: 0;
  }
   .lgf-progress .lgf-progress-right .lgf-progress-bar {
       left: -100%;
       border-top-left-radius: 75px;
       border-bottom-left-radius: 75px;
       border-right: 0;
       -webkit-transform-origin: center right;
       transform-origin: center right;
  }
   .lgf-progress .lgf-progress-value {
       display: flex;
       border-radius: 50%;
       font-size: 36px;
       text-align: center;
       line-height: 20px;
       align-items: center;
       justify-content: center;
       height: 100%;
       font-weight: 300;
  }
   .lgf-progress .lgf-progress-value div {
       margin-top: 10px;
  }
   .lgf-progress .lgf-progress-value span {
       font-size: 12px;
  }
  /* This for loop creates the necessary css animation names Due to the split circle of lgf-progress-left and lgf-progress right, we must use the animations on each side. */
   .lgf-progress[data-percentage="10"] .lgf-progress-right .lgf-progress-bar {
       animation: loading-1 1.5s linear forwards;
  }
   .lgf-progress[data-percentage="10"] .lgf-progress-left .lgf-progress-bar {
       animation: 0;
  }
   .lgf-progress[data-percentage="20"] .lgf-progress-right .lgf-progress-bar {
       animation: loading-2 1.5s linear forwards;
  }
   .lgf-progress[data-percentage="20"] .lgf-progress-left .lgf-progress-bar {
       animation: 0;
  }
   .lgf-progress[data-percentage="30"] .lgf-progress-right .lgf-progress-bar {
       animation: loading-3 1.5s linear forwards;
  }
   .lgf-progress[data-percentage="30"] .lgf-progress-left .lgf-progress-bar {
       animation: 0;
  }
   .lgf-progress[data-percentage="40"] .lgf-progress-right .lgf-progress-bar {
       animation: loading-4 1.5s linear forwards;
  }
   .lgf-progress[data-percentage="40"] .lgf-progress-left .lgf-progress-bar {
       animation: 0;
  }
   .lgf-progress[data-percentage="50"] .lgf-progress-right .lgf-progress-bar {
       animation: loading-5 1.5s linear forwards;
  }
   .lgf-progress[data-percentage="50"] .lgf-progress-left .lgf-progress-bar {
       animation: 0;
  }
   .lgf-progress[data-percentage="60"] .lgf-progress-right .lgf-progress-bar {
       animation: loading-5 1.5s linear forwards;
  }
   .lgf-progress[data-percentage="60"] .lgf-progress-left .lgf-progress-bar {
       animation: loading-1 1.5s linear forwards 1.5s;
  }
   .lgf-progress[data-percentage="70"] .lgf-progress-right .lgf-progress-bar {
       animation: loading-5 1.5s linear forwards;
  }
   .lgf-progress[data-percentage="70"] .lgf-progress-left .lgf-progress-bar {
       animation: loading-2 1.5s linear forwards 1.5s;
  }
   .lgf-progress[data-percentage="80"] .lgf-progress-right .lgf-progress-bar {
       animation: loading-5 1.5s linear forwards;
  }
   .lgf-progress[data-percentage="80"] .lgf-progress-left .lgf-progress-bar {
       animation: loading-3 1.5s linear forwards 1.5s;
  }
   .lgf-progress[data-percentage="90"] .lgf-progress-right .lgf-progress-bar {
       animation: loading-5 1.5s linear forwards;
  }
   .lgf-progress[data-percentage="90"] .lgf-progress-left .lgf-progress-bar {
       animation: loading-4 1.5s linear forwards 1.5s;
  }
   .lgf-progress[data-percentage="100"] .lgf-progress-right .lgf-progress-bar {
       animation: loading-5 1.5s linear forwards;
  }
   .lgf-progress[data-percentage="100"] .lgf-progress-left .lgf-progress-bar {
       animation: loading-5 1.5s linear forwards 1.5s;
  }
   @keyframes loading-1 {
       0% {
           -webkit-transform: rotate(0deg);
           transform: rotate(0deg);
      }
       100% {
           -webkit-transform: rotate(36);
           transform: rotate(36deg);
      }
  }
   @keyframes loading-2 {
       0% {
           -webkit-transform: rotate(0deg);
           transform: rotate(0deg);
      }
       100% {
           -webkit-transform: rotate(72);
           transform: rotate(72deg);
      }
  }
   @keyframes loading-3 {
       0% {
           -webkit-transform: rotate(0deg);
           transform: rotate(0deg);
      }
       100% {
           -webkit-transform: rotate(108);
           transform: rotate(108deg);
      }
  }
   @keyframes loading-4 {
       0% {
           -webkit-transform: rotate(0deg);
           transform: rotate(0deg);
      }
       100% {
           -webkit-transform: rotate(144);
           transform: rotate(144deg);
      }
  }
   @keyframes loading-5 {
       0% {
           -webkit-transform: rotate(0deg);
           transform: rotate(0deg);
      }
       100% {
           -webkit-transform: rotate(180);
           transform: rotate(180deg);
      }
  }
   .lgf-progress {
       margin-bottom: 1em;
  }
  
  .lgf-progress.width-20 {
       width: 120px !important;
